<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form :inline="true" :model="searchValidateForm" ref="searchValidateForm" label-width="100px" class="search-validate-form" :rules="searchValidateFormRules">
          <!-- 请选择公司 start -->
          <el-form-item label="" prop="companyId">
            <el-select v-model="searchValidateForm.companyId" filterable placeholder="请选择公司" clearable @change="onChangeSearchCompany">
              <el-option v-for="(item, index) in searchCompanyOptions" :key="index" :label="item.COMPANY_NAME" :value="item.COMPANY_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择公司 end -->

          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select v-model="searchValidateForm.spotId" filterable placeholder="请选择景区" clearable @change="onChangeSearchSpot">
              <el-option v-for="(item, index) in searchScenicOptions" :key="index" :label="item.SCENIC_SPOT_NAME" :value="item.SCENIC_SPOT_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 按订单数量统计 start -->
          <!-- <el-form-item label="" prop="type">
            <el-select v-model="searchValidateForm.type" filterable placeholder="请选择统计状态" @change="onChangeSearchType">
              <el-option v-for="(item, index) in typeOptions" :key="index" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item> -->
          <!-- 按订单数量统计 end -->

          <!-- 请选择日期统计方式 start -->
          <!-- <el-form-item label="" prop="">
            <el-select v-model="selectDateState" filterable placeholder="请选择日期统计方式" @change="onDateChange">
              <el-option v-for="(item, index) in dateStateOptions" :key="index" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item> -->
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <!-- <el-form-item label="" prop="startEndDate">
            <el-date-picker v-model="searchValidateForm.startEndDate" unlink-panels :type="dateType" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :value-format="valueFormat">
            </el-date-picker>
          </el-form-item> -->
          <el-form-item label="" prop="startEndDate">
            <el-date-picker v-model="searchValidateForm.startEndDate" unlink-panels type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :value-format="valueFormat">
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->

        </el-form>
        <!-- 搜索表单 end -->

        <!-- 重置 start -->
        <!-- <el-button type="primary" icon="el-icon-refresh-right" @click="onReset">重置</el-button> -->
        <!-- 重置 end -->

        <!-- 统计报表 start -->
        <el-button type="primary" icon="el-icon-data-line" @click="onStatGrossProfit('searchValidateForm')">统计报表</el-button>
        <!-- 统计报表 end -->

        <!-- 导出报表 start -->
        <a href="" id="exportReport" download="订单数量">
          <el-button type="primary" icon="el-icon-s-data" @click="onExportReport">导出报表</el-button>
        </a>
        <!-- 导出报表 end -->

        <!-- 查询数据 start -->
        <el-button type="primary" icon="el-icon-search" @click="onSearch('searchValidateForm')">查询数据</el-button>
        <!-- 查询数据 end -->

        <!-- 导出 Excel start -->
        <el-button type="primary" :loading="downloadLoading" icon="el-icon-s-data" @click="exportExcel">导出数据</el-button>

        <!-- 隐藏的a标签，用来导出excel -->
        <a id="exportA" class="exportA" :href="downloadUrl" download></a>
        <!-- 导出 Excel end -->

        <!-- 新增 start -->
        <!-- <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">新增</el-button> -->
        <!-- 新增 end -->
      </el-header>
      <!-- 显示表格 start -->
      <div v-show="showTable">
        <el-main>
          <!-- 表格 start -->
          <el-table class="table" :data="tableData" style="width: 100%" border v-loading="tableLoading">
            <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" min-width="130">
            </el-table-column>
          </el-table>
          <!-- 表格 end -->

          <!-- 导出的表格 start -->
          <!-- 此表格始终保持隐藏 -->
          <el-table id="outTable" class="table" :data="exportTableData" style="width: 100%; display: none" border>
            <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" min-width="130" align="center">
            </el-table-column>
          </el-table>
          <!-- 导出的表格 end -->
        </el-main>
        <el-footer>
          <!-- 分页 start -->
          <!-- <div class="block">
          <el-pagination background layout="prev, pager, next, total, sizes, jumper" :total="total" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" @current-change="onCurrentChange" @size-change="onSizeChange">
          </el-pagination>
        </div> -->
          <!-- 分页 end -->
        </el-footer>
      </div>
      <!-- 显示表格 end -->

      <!-- 显示柱状图 start -->
      <div v-show="!showTable" class="my-echarts">
        <!-- v-loading="tableLoading" -->
        <div id="myChart"></div>
      </div>
      <!-- 显示柱状图 start -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatMonth, createUrl } from "../utils/utils.js"
export default {
  name: 'SettlementFlow',
  data() {
    /**
   * 公共验证函数(验证数字值)
   * @params message - 提示信息
   */
    function checkNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback();
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填数字值)
    * @params message - 提示信息
    */
    function checkRequireNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填值)
    * @params message - 提示信息
    */
    function checkRequireMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        } else {
          callback();
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: "", // 搜索的手续费
      searchTaxInput: "", // 搜索的税点
      tableFields: { //导出Excel表格的表头设置
        "订单状态": "订单状态",
        "订单数量": "订单数量",
        "订单计费金额/元": "订单计费金额",
        "退款金额/元": "退款金额",
        "实际收入金额/元": "实际收入金额",
        "最终入账金额/元": "最终入账金额",
      },
      tableData: [{
        订单状态: '进行中',
        订单数量: 0,
        订单计费金额: 0,
        退款金额: 0,
        实际收入金额: 0,
        最终入账金额: 0,
      }], // 表格数据
      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项

      dateStateOptions: [{
        value: '1',
        label: '按月查询'
      }, {
        value: '2',
        label: '按日查询'
      }, {
        value: '3',
        label: '按年查询'
      }], // 日期统计方式选项
      typeOptions: [{
        value: '1',
        label: '按订单数量统计'
      }, {
        value: '2',
        label: '按订单计费金额统计'
      }], // 景区选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示编辑dialog对话框

      dialogState: "", // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      selectDateState: "1", // 日期统计方式（月，日，年）
      dateType: "monthrange", // 日期类型（月，日，年）
      valueFormat: "yyyy-MM-dd", // 日期格式

      searchValidateForm: {
        companyId: "", // 公司id
        spotId: "", // 景区id
        startEndDate: "", // 起止日期
        type: "1", // 订单统计状态
      }, // 搜索表单验证对象
      searchVF: {
        companyId: "", // 公司id
        spotId: "", // 景区id
        startEndDate: "", // 起止日期
        type: "1", // 订单统计状态
      }, // 搜索需要提交的表单
      validateForm: {}, // 表单验证对象

      searchPartnerCompanyName: "", // 搜索的公司
      searchScenicName: "", // 搜索的景区
      searchSelectDateState: "1", // 搜索的日期统计方式
      searchStartEndDate: "", // 搜索的起止时间
      searchPayState: "", // 搜索的支付状态

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      downloadUrl: "",

      total: 0,

      myChart: null, // 左侧饼图echarts实例
      markLineData: [], // 标线的数据数组
      option: {}, // 左侧饼图optino

      // 搜索表单规则
      searchValidateFormRules: {

      },

      // 提交表单规则
      validateFormRules: {
        // 付款金额
        moneyOut: [
          { validator: checkNumberMethod('付款金额'), trigger: ['blur', 'change'] }
        ],

        // 收款金额
        moneyIn: [
          { validator: checkNumberMethod('收款金额'), trigger: ['blur', 'change'] }
        ],
      },

    }
  },
  created() {
    this.getValidateForm()
    // this.login()
    this.getCompanyList()
    this.getScenicList()
    this.initTableData()
  },
  beforeMount() {

  },
  mounted() {
    this.drawEcharts()
  },
  methods: {
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ""
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post("/loginSystem?username=lijiazhao&password=lijiazhao")
    },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get("/system/robot_time_statistics/companyList", {
        params: {
          spotId: spotId
        }
      })
      if (res.state == "200") {
        // console.log(res);
        this.searchCompanyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get("/system/robot_time_statistics/spotList", {
        params: {
          companyId: companyId
        }
      })
      if (res.state == "200") {
        // console.log(res);
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var companyId = this.searchVF.companyId
      if (companyId == "" || !companyId) {
        companyId = null
      }

      var spotId = this.searchVF.spotId
      if (spotId == "" || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchVF.startEndDate
      var beginTime, endTime
      if (startEndDate == "" || !startEndDate) {
        // beginDate = formatMonth()
        // endDate = formatMonth()
        beginTime = null
        endTime = null
      } else {
        beginTime = this.searchVF.startEndDate[0]
        endTime = this.searchVF.startEndDate[1]
      }

      this.tableLoading = true

      const { data: res } = await this.$http.get("/system/order_statistics/findData", {
        params: {
          company: companyId,
          spot: spotId,
          beginTime: beginTime,
          endTime: endTime,
        }
      })
      // console.log(res);
      this.tableData = res.data
      this.total = res.total

      this.tableLoading = false

      // 隐藏echart的loadnig效果
      this.myChart.hideLoading();

      var resultData = res.data.concat()
      resultData.forEach((item, index) => {
        if (this.searchVF.type == "2" && item.订单状态 == "实际收入金额") {
          resultData.unshift(resultData.splice(index, 1)[0])
        } else {
          if (item.订单状态 == "已付款") {
            resultData.unshift(resultData.splice(index, 1)[0])
          }
        }
      })

      var source = resultData.map((item) => {
        return [
          item.订单状态,
          item.订单数量
        ]
      })

      source.unshift(['order', '已付款订单', '其他订单'])
      this.addOtherData(source, 5);
      this.option.dataset.source = source
      this.option.series[0].markLine.data = this.markLineData
      this.myChart.setOption(this.option)
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      const { data: res } = await this.$http.get("/system/order_statistics/findData")
      if (res.state == "200") {
        this.exportTableData = res.data
      } else {
        return
      }
    },

    /**
     * 编辑签约信息
     */
    async editTableData(data) {
      const { data: res } = await this.$http.post("/system/account_close/update", data)
      // console.log(res);
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: "success",
          offset: 100,
          center: true,
          duration: 1000,
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: "error",
          offset: 100,
          center: true,
          duration: 3000,
        })
      }
    },

    /**
     * 获取表标线 对应点坐标
     * @params percent - "其他"占比
     */
    getMarkLineData(percent) {
      // 1.获取画布 width,height
      let height = this.myChart.getHeight()
      let width = this.myChart.getWidth()

      // 2.  根据 series[0].center 获取圆心坐标
      let x0 = width * 0.25 // 圆心x轴坐标

      //3.圆边上点坐标
      // let x1   =   x0   +   r   *   cos(ao   *   3.14   /180   )
      // let y1   =   y0   +   r   *   sin(ao   *   3.14   /180   )

      // “其他” 终点坐标series[0].startAngle 20
      let x1 = x0 + (height / 4) * Math.cos(20 * 3.14 / 180)
      let y1 = (height * 0.5) - (height / 4) * Math.sin(20 * 3.14 / 180)

      let ao = 360 * (percent / 100) // 扇形角度

      let ao1 = 0 // 用来计算的坐标角度
      ao1 = (ao <= 20) ? (20 - ao) : (360 - (ao - 20))
      if (ao1 < 270 && ao1 > 20) ao1 = 270 // 角度当270用，要不样式不好看

      let x2 = 0, y2 = 0;
      x2 = x0 + (height / 4) * Math.cos(ao1 * 3.14 / 180)
      y2 = (height * 0.5) - (height / 4) * Math.sin(ao1 * 3.14 / 180)

      return [[{ x: x1, y: y1 }, { x: "75%", y: "35%" }], [{ x: x2, y: y2 }, { x: "75%", y: "65%" }]]
    },

    /**
     * 添加其他 
     * @params datasetSource - echarts的source
     * @params len - "其他"的个数
     */
    addOtherData(datasetSource, len) {
      let percent = 0
      let sum = 0 // 总计
      datasetSource.forEach((data, rowIndex) => {
        if (rowIndex > 0) { // 第一行数据不算
          let count = 0
          for (let key of data) {
            let value = isNaN(key) ? 0 : Number(key)
            if (count === 1) sum += value
            count++
          }
        }
      })
      let endData = datasetSource.slice(datasetSource.length - len)
      let other = ["其他"]
      for (let i = 0; i < endData.length; i++) {
        let j = 0;
        for (let key of endData[i]) {
          let value = isNaN(key) ? 0 : key
          if (j) other[j] ? (other[j] += value) : other.push(value)
          j++
        }
        endData[i].splice(1, 0, "")
      }
      datasetSource.push(other)
      // "其他"占比
      percent = sum ? ((other[1] / sum) * 100).toFixed(2) : 100
      this.markLineData = this.getMarkLineData(percent)
    },

    /**
     * 画Echarts图表
     */
    drawEcharts() {
      this.myChart = this.$echarts.init(document.getElementById('myChart'))

      let source = [
        ['order', '已付款订单', '其他订单'],
        ['已付款', 944],
        ['进行中', 50],
        ['交易关闭', 16],
        ['免单', 20],
        ['全额退款', 9],
        ['未付款', 11],
      ]

      // 添加“其他”
      this.addOtherData(source, 5);

      this.option = {
        title: {
          text: "订单数量",
          subtext: "Order Number",
          left: "center"
        },
        legend: {
          // orient: 'vertical',
          left: "center",
          bottom: "0"
        },
        tooltip: {},
        dataset: {
          source: source
        },
        series: [{
          name: "已付款订单数量统计",
          type: 'pie',
          radius: "50%",
          center: ['25%', '50%'],
          selectedMode: 'single',
          label: {
            show: true,
            position: "inside",
            formatter: '{b}: {@已付款订单} ({d}%)'
          },
          startAngle: 20, // 起始角度 20 
          clockwise: false, // 逆时针 
          markLine: {
            lineStyle: {
              type: 'solid',
              color: "#BFBFBF"
            },
            symbol: 'none',
            data: this.markLineData
          }
        },
        {
          name: "其他订单数量统计",
          type: 'pie',
          radius: "30%",
          center: ['75%', '50%'],
          selectedMode: 'single',
          encode: {
            itemName: 'order',
            value: '其他订单',
          },
          label: {
            show: true,
            // position: "inside",
            formatter: '{b}: {@其他订单} ({d}%)'
          },
        }
        ]
      };

      this.myChart.setOption(this.option);

      var that = this
      window.addEventListener("resize", function () {
        // that.myChart.resize()
        // that.addOtherData(source, 5);
        // that.option.series[0].markLine.data = that.markLineData
        // that.myChart.setOption(that.option)
      })
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (this.searchValidateForm.companyId == this.searchVF.companyId && this.searchValidateForm.spotId == this.searchVF.spotId && this.selectDateState == this.searchSelectDateState && this.searchValidateForm.startEndDate == this.searchVF.startEndDate && this.searchValidateForm.type == this.searchVF.type) {
        // 隐藏echart的loadnig效果
        this.myChart.hideLoading();
        return
      }

      // 保存当前搜索的内容
      this.searchVF.companyId = this.searchValidateForm.companyId
      this.searchVF.spotId = this.searchValidateForm.spotId
      this.searchSelectDateState = this.selectDateState
      this.searchVF.startEndDate = this.searchValidateForm.startEndDate
      this.searchVF.type = this.searchValidateForm.type

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log("调用接口");
      this.initTableData()
    },

    /** 搜索表单中选项变化方法 start */
    // 公司选项变化时触发
    async onChangeSearchCompany(companyId) {
      var res = await this.getScenicList(companyId)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/scenicList 接口返回的景区列表为空"
        // })
        this.searchValidateForm.spotId = ""
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SCENIC_SPOT_ID == this.searchValidateForm.spotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      var spotId = searchScenicOptions[0].SCENIC_SPOT_ID
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.spotId = spotId
      }
    },

    // 景区选项变化时触发
    async onChangeSearchSpot(spotId) {
      // 如果spotId为空，则将支付方式置为空
      if (!spotId) {
        this.searchValidateForm.paymentType = []
      }
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      this.searchCompanyOptions = searchCompanyOptions

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/companyList 接口返回的公司列表为空"
        // })
        this.searchValidateForm.companyId = ""
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.companyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.companyId = searchCompanyOptions[0].COMPANY_ID
      }
    },

    onChangeSearchType(type) {
      console.log(type);
      if (type == 1) {
        // 按订单数量统计
        this.tableFields = {
          "订单状态": "订单状态",
          "订单数量": "订单数量",
          "订单计费金额/元": "订单计费金额",
          "退款金额/元": "退款金额",
          "实际收入金额/元": "实际收入金额",
          "最终入账金额/元": "最终入账金额",
        }
      } else if (type == 2) {
        // 按订单计费金额统计
        this.tableFields = { //导出Excel表格的表头设置
          "订单状态": "订单状态",
          "订单数量": "订单数量",
          "订单计费金额/元": "订单计费金额",
        }
      }
    },
    /** 搜索表单中选项变化方法 end */

    // 日期选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState);
      if (this.selectDateState == "1") { // 按月查询
        this.dateType = "monthrange"
        this.valueFormat = "yyyy-MM"
      } else if (this.selectDateState == "2") { // 按日查询
        this.dateType = "daterange"
        this.valueFormat = "yyyy-MM-dd"
      } else if (this.selectDateState == "3") { // 按年查询
        this.dateType = "yearrange"
        this.valueFormat = "yyyy"
      }
    },


    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.companyId = ""
      this.searchValidateForm.spotId = ""
      this.searchValidateForm.startEndDate = ""
      this.selectDateState = "1"
      this.dateType = "monthrange"

      // 重新获取表格数据
      this.getTableData()
      // 重新获取搜索公司列表
      this.getCompanyList()
      // 重新获取搜索景区列表
      this.getScenicList()
    },

    // 统计报表(显示柱状图)
    onStatGrossProfit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = false
          // console.log(this.searchValidateForm);
          // echarts loading加载
          this.myChart.showLoading({
            text: 'loading',
            color: '#c23531',
            textColor: '#000',
            maskColor: 'rgba(255, 255, 255, 0.2)',
            zlevel: 0,
          })
          this.getTableData()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // 导出报表（导出柱状图）
    onExportReport() {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById("exportReport")
        exportReportEle.href = this.myChart.getDataURL()
        // exportReportEle.click()
      })
    },

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true
          // console.log(this.searchValidateForm);
          this.getTableData()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        this.downloadLoading = true
        await this.getExportTableData()
        // 使用nextTick当table表格渲染完成后执行导出表格操作
        this.$nextTick(() => {
          var xlsxParam = { raw: true } //转换成excel时，使用原始的格式
          /* 从表生成工作簿对象 */
          var wb = XLSX.utils.table_to_book(
            document.querySelector('#outTable'),
            xlsxParam
          )
          /* 获取二进制字符串作为输出 */
          var wbout = XLSX.write(wb, {
            bookType: 'xlsx',
            bookSST: true,
            type: 'array'
          })
          try {
            FileSaver.saveAs(
              //Blob 对象表示一个不可变、原始数据的类文件对象。
              //Blob 表示的不一定是JavaScript原生格式的数据。
              //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
              //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
              new Blob([wbout], {
                type: 'application/octet-stream'
              }),
              //设置导出文件名称
              "订单统计状态" + '.xlsx'
            )
          } catch (e) {
            if (typeof console !== 'undefined') console.log(e, wbout)
          }
          this.downloadLoading = false
          return wbout
        })

        this.$message({
          type: 'success',
          message: '已成功导出Excel表格！',
          offset: 100,
          center: true,
          duration: 1000,
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消导出Excel表格',
          offset: 100,
          center: true,
          duration: 1000,
        });
      })

    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = "add"
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框

      var rowObj = Object.assign({}, row) // 深拷贝row对象
      // rowObj.orderAmount = parseInt(rowObj.orderAmount)
      // rowObj.charge = parseInt(rowObj.charge)
      // rowObj.totalCharge = parseInt(rowObj.totalCharge)
      // rowObj.proportion = parseInt(rowObj.proportion)
      // rowObj.totalProportion = parseInt(rowObj.totalProportion)
      // rowObj.tax = parseInt(rowObj.tax)
      // rowObj.taxDeStreamProfitSharingExpenditureAmount = parseInt(rowObj.taxDeStreamProfitSharingExpenditureAmount)
      // rowObj.robotCount = parseInt(rowObj.robotCount)
      // rowObj.repair = parseInt(rowObj.repair)
      // rowObj.moneyOut = parseInt(rowObj.moneyOut)

      // rowObj.paymentName = "张三" // 当前登录用户
      this.validateForm = rowObj
    },

    // dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var data = Object.assign({}, this.validateForm) // 防止数据错乱先深拷贝一波

          console.log("编辑公司信息");
          this.editTableData(data)

          this.dialogVisible = false
          this.$refs.validateForm.resetFields(); // 重置表单
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
  }
}
</script>

